var obj = {
  data: {
    uuid: 'a70f537b-d0f1-4553-81bb-1767acf549d1',
    products: [],
  },
  getData: function() {
    var vm = this;
    var url = `https://course-ec-api.hexschool.io/api/${this.data.uuid}/ec/products`;
    axios.get(url)
      .then(function (response) {
        vm.data.products = response.data.data;
        vm.render();
      })
  },
  render: function() {
    var app = document.getElementById('serviceItems');
    var products = this.data.products;
    var str = '';
    console.log('products', products);
    products.forEach(function(item) {
      str += `
        <div class="col-xl-6 col-xxl-4 mb-12 px-6">
          <div class="h-100 card c-card c-card--hoverShadow border-0">
            <div class="c-pseudoPhoto c-card__photo--hoverZoomIn" style="background-image: url(${ item.imageUrl[0] });"></div>
            <div class="card-body p-8">
              <h5 class="card-title">${ item.title }</h5>
              <p class="card-text">${ item.content }</p>
            </div>
          </div>
        </div>
      `;
    });
    app.innerHTML = str;
  }
}
obj.getData();